.input-area {
  background-color: #191e28;
  padding: 20px;
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  border-top: 1px solid #444b58;
}
.form {
  display: flex;
  align-items: center;
}
.input-area input {
  direction: rtl;
  background-color: rgba(255, 255, 255, 0.486);
  border: none;
  border-radius: 20px;
  padding: 1vw;
  width: 80vw;
  padding-right: 5vw;
  font-size: 22px;
}
.send-button {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  margin: 10px;
  background-color: white;
}
.send-button:disabled {
  background: rgb(121, 121, 121);
}
.send-button > svg {
  width: 100%;
  height: 60%;
  padding-left: 15%;
}
