@import url(https://fonts.googleapis.com/css?family=PT+Mono);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=PT+Mono);
* {
  font-family: "PT Mono", "Courier";
  /* font-family: "Montserrat", sans-serif; */
  box-sizing: border-box;
  outline: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #07080e;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

::-webkit-scrollbar {
  width: 6px; /* width of the entire scrollbar */
}
::-webkit-scrollbar-track {
  background: transparent; /* color of the tracking area */
}
::-webkit-scrollbar-thumb {
  background-color: white; /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
}


.timer > svg {
  display: block;
  margin: 50px auto;
}
.circle-background {
  border-radius: 50%;
  background-color: white;
}
#loader {
  fill: gray;
}

#border {
  fill: gray;
}
.flashing-timer {
  animation: flashing 1s infinite linear;
}
@keyframes flashing-timer {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.message-container {
  display: flex;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(26, 29, 39, 1) 100%);
  margin: 7px 10px;
  direction: rtl;
  width: 70vw;
  padding: 10px;
}
.message-name {
  background-color: white;
  padding: 1px 10px;
  font-weight: bold;
  margin-left: 10px;
}
.message-context {
  margin-right: 10px;
  font-size: 20px;
  color: #7a7f91;
}
.message-ended {
  animation: transY 0.5s linear, opacityAnimate 0.5s linear, display 0.5s 0.5s linear;
  /* display: none; */
}

@keyframes transY {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-80px);
  }
}
@keyframes opacityAnimate {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes display {
  from {
    display: block;
  }
  to {
    display: none;
  }
}

.chat {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: calc(100vh - 80px - 60px);
  overflow-y: auto;
  padding: 10px 0px;
}

.header {
  height: 80px;
  background: radial-gradient(
      farthest-corner at top,
      #27375c 0%,
      #282f3f 4%,
      #212632 12.25%,
      #1a1f2b 31.25%,
      #181b20 50%
    )
    top right/200% 200%;
  animation: colorChange 5s infinite alternate;
}

@keyframes colorChange {
  to {
    background-position: top left;
  }
}
.header-top {
  display: flex;
  align-items: center;
  padding: 10px 0px 10px 10px;
}

.circle-progress {
  height: 20px;
  width: 20px;
  border: 2px solid white;
  border-right: 2px solid transparent;
  border-radius: 50%;
  left: 0;
  top: 0;
  animation: spin 1.5s infinite linear;
}
.title {
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  letter-spacing: 3px;
  /* color: #75787e; */
  color: white;
  text-transform: uppercase;
  margin-left: 10px;
}

.information {
  display: flex;
  align-items: center;
  padding: 10px 0px 10px 10px;
}
.square {
  width: 10px;
  height: 10px;
  background-color: white;
  animation: flashing 1s infinite linear;
}
.hash {
  text-transform: uppercase;
  color: white;
  margin-left: 10px;
}
.hash::after {
  content: " HASH";
}
.users,
.room {
  direction: rtl;
  text-transform: uppercase;
  color: #7a7f91;
}
.users {
  margin-left: auto;
}
.room {
  margin: 0px 20px;
}
.users::before {
  content: "משתמשים: ";
  color: white;
}
.room::before {
  content: "חדר: ";
  color: white;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes flashing {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  70% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Progress Bar */
.progress {
  position: relative;
  height: 3px;
  display: block;
  width: 100%;
  background-color: #88a2c5;
  border-radius: 2px;
  background-clip: padding-box;
  overflow: hidden;
}

.progress .indeterminate {
  background-color: #ccd1d8;
}
.progress .indeterminate:before {
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  animation: indeterminate 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;
}
.progress .indeterminate:after {
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  left: 0;
  bottom: 0;
  will-change: left, right;
  animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;
  animation-delay: 1.15s;
}
@media (max-width: 500px) {
  .hash {
    display: none;
  }
}
@keyframes indeterminate {
  0% {
    left: -35%;
    right: 100%;
  }
  60% {
    left: 100%;
    right: -90%;
  }
  100% {
    left: 100%;
    right: -90%;
  }
}
@keyframes indeterminate-short {
  0% {
    left: -200%;
    right: 100%;
  }
  60% {
    left: 107%;
    right: -8%;
  }
  100% {
    left: 107%;
    right: -8%;
  }
}

.input-area {
  background-color: #191e28;
  padding: 20px;
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  border-top: 1px solid #444b58;
}
.form {
  display: flex;
  align-items: center;
}
.input-area input {
  direction: rtl;
  background-color: rgba(255, 255, 255, 0.486);
  border: none;
  border-radius: 20px;
  padding: 1vw;
  width: 80vw;
  padding-right: 5vw;
  font-size: 22px;
}
.send-button {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  margin: 10px;
  background-color: white;
}
.send-button:disabled {
  background: rgb(121, 121, 121);
}
.send-button > svg {
  width: 100%;
  height: 60%;
  padding-left: 15%;
}

.container {
  margin-left: 20px;
  width: 220px;
  height: 100%;
  color: white;
  font-family: "PT Mono", "Courier";
  font-weight: 400;
  letter-spacing: 1px;
  font-size: 2%;
  overflow: hidden;
  text-shadow: 0 0 2px #fff;
  position: absolute;
}
.container > div {
  animation: run 1s linear 0s infinite;
  width: 100%;
}

.container::before {
  position: absolute;
  content: "";
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(7, 8, 14, 1) 50%);

  width: 400px;
  height: 50px;
  z-index: 2;
}

.container::after {
  position: absolute;
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(7, 8, 14, 1) 50%);
  width: 400px;
  height: 50px;
  z-index: 2;
  bottom: 0;
}

@keyframes run {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100px);
  }
}

.code {
  position: absolute;
  top: 100px;
}
.code > .container {
  height: 300px;
}

