.message-container {
  display: flex;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(26, 29, 39, 1) 100%);
  margin: 7px 10px;
  direction: rtl;
  width: 70vw;
  padding: 10px;
}
.message-name {
  background-color: white;
  padding: 1px 10px;
  font-weight: bold;
  margin-left: 10px;
}
.message-context {
  margin-right: 10px;
  font-size: 20px;
  color: #7a7f91;
}
.message-ended {
  animation: transY 0.5s linear, opacityAnimate 0.5s linear, display 0.5s 0.5s linear;
  /* display: none; */
}

@keyframes transY {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-80px);
  }
}
@keyframes opacityAnimate {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes display {
  from {
    display: block;
  }
  to {
    display: none;
  }
}
