@import url("https://fonts.googleapis.com/css?family=PT+Mono");

.container {
  margin-left: 20px;
  width: 220px;
  height: 100%;
  color: white;
  font-family: "PT Mono", "Courier";
  font-weight: 400;
  letter-spacing: 1px;
  font-size: 2%;
  overflow: hidden;
  text-shadow: 0 0 2px #fff;
  position: absolute;
}
.container > div {
  animation: run 1s linear 0s infinite;
  width: 100%;
}

.container::before {
  position: absolute;
  content: "";
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(7, 8, 14, 1) 50%);

  width: 400px;
  height: 50px;
  z-index: 2;
}

.container::after {
  position: absolute;
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(7, 8, 14, 1) 50%);
  width: 400px;
  height: 50px;
  z-index: 2;
  bottom: 0;
}

@keyframes run {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100px);
  }
}
