.timer > svg {
  display: block;
  margin: 50px auto;
}
.circle-background {
  border-radius: 50%;
  background-color: white;
}
#loader {
  fill: gray;
}

#border {
  fill: gray;
}
.flashing-timer {
  animation: flashing 1s infinite linear;
}
@keyframes flashing-timer {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
